import React, { useState } from 'react';
import style from './RegisterView.module.css';
import { useSelector,useDispatch } from 'react-redux';
import { authOperations } from '../../redux/auth';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
//import { authSelectors } from '../../redux/auth';

export default function RegisterView() {

    const dispatch = useDispatch();
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const handleChangeName = ({target:{value}}) => {
    
     setName(value );
    };

    const handleChangeEmail = ({target:{value}}) => {
    
        setEmail(value );
    };

   const handleChangePassword = ({target:{value}} ) => {
    
        setPassword( value );
    };

    const handleSubmit = e => {
        e.preventDefault();
              
        if(name.trim() ===''  ){
           toast.error('–í–≤–µ–¥–∏—Ç–µ –ò–º—è');
        return;
         }
          if (email.trim() ===''){
            toast.error('–í–≤–µ–¥–∏—Ç–µ –ü–æ—á—Ç—É ');
        return;
        }          
       if(password.trim() ===''  ){
           toast.error('–í–≤–µ–¥–∏—Ç–µ –ü–∞—Ä–æ–ª—å');
        return;
       }
        
        dispatch(authOperations.register({name,email,password}));
        setName('');
        setEmail('');
        setPassword('');
    };



        return (
            <div >
                <h1 className={style.title}>–°—Ç—Ä–∞–Ω–∏—Ü–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏</h1>
                <div >
                <form className={style.form}
                    onSubmit={handleSubmit}
                    
                        autoComplete='off'>
                        <div >
                    <label  className={style.label}>
                        –ò–º—è  
                        <input
                            type="text"
                            name="name"
                            value={name}
                            onChange={handleChangeName} />
                    </label>
                        </div>
                        <div >
                    <label className={style.label}>
                        –ü–æ—á—Ç–∞  
                        <input 
                            type="email"
                            name="email"
                            value={email}
                            onChange={handleChangeEmail} />
                            </label>
                        </div>
                        <div >
                    <label className={style.label}>
                        –ü–∞—Ä–æ–ª—å  
                        <input 
                            type="password"
                            name="password"
                            value={password}
                            onChange={handleChangePassword} />
                            </label>
                        </div>
                        <div >
                    <button className={style.Button} type="submit" >–ó–∞—Ä–µ–≥–µ—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
                        </div>
                        
                    </form>
                   </div>
            </div>
        );
   
};


